[class*="mobile.grid"] {
    display: flex;
    flex-wrap: wrap;
}

[class*="mobile.grid(1"] {
    display: block;

    > * {
        width: 100%;
    }
}

[class*="mobile.grid"] > :last-child {
    margin-bottom: 0;
}

[class*="mobile.grid(2"],
[class*="mobile.grid(3"],
[class*="mobile.grid(4"],
[class*="mobile.grid(5"],
[class*="mobile.grid(6"] {
    > :nth-last-child(2) {
        margin-bottom: 0;
    }
}

[class*="mobile.grid(3"],
[class*="mobile.grid(4"],
[class*="mobile.grid(5"],
[class*="mobile.grid(6"] {
    > :nth-last-child(3) {
        margin-bottom: 0;
    }
}

[class*="mobile.grid(4"],
[class*="mobile.grid(5"],
[class*="mobile.grid(6"] {
    > :nth-last-child(4) {
        margin-bottom: 0;
    }
}

[class*="mobile.grid(5"],
[class*="mobile.grid(6"] {
    > :nth-last-child(5) {
        margin-bottom: 0;
    }
}

[class*="mobile.grid(6"] {
    > :nth-last-child(6) {
        margin-bottom: 0;
    }
}

@for $cols from 1 through 6 {
    $colSize: percentage(1) / $cols;

    .mobile\.grid\(#{$cols}\,0\,0\) {
        > * {
            width: $colSize;
        }
    }
}

@for $rowGap from 1 through 60 {
    $rgap: $rowGap * 5;

    .mobile\.grid\(1\,0\,#{$rgap}\) {
        > * {
            margin-bottom: #{$rgap}px;
        }
    }
}



@for $cols from 2 through 6 {

  [class*="mobile.grid(#{$cols}"] {
      > :nth-child(#{$cols}n) {
          margin-right: 0;
      }
  }

    @for $colGap from 0 through 10 {
        @for $rowGap from 0 through 10 {

          @if $colGap == 0 and $rowGap == 0 {

          }
          @else {

            $cgap: $colGap * 5;
            $rgap: $rowGap * 5;
            $colSize: percentage(1) / $cols;
            $colSizeAdjust: #{$cgap - ($cgap / $cols)}px;

            .mobile\.grid\(#{$cols}\,#{$cgap}\,#{$rgap}\) {
                > * {
                    width: calc(#{$colSize} - #{$colSizeAdjust});
                    margin-right: #{$cgap}px;
                    margin-bottom: #{$rgap}px;
                }
            }

          }
        }
    }
}
